<style scoped>
.cashbox >>> .ivu-tabs-content{
    min-height: 433px;
}
</style>
<template>
  <div>
    <div class="cashbox">
      <Tabs value="Bookkeeping" @on-click="changetab" :animated="false">
        <TabPane :label="Bookkeepinglabel" name="Bookkeeping">
          <Spin size="large" fix v-show="spinShow==true"></Spin>
          <Bookkeeping ref="Bookkeeping" 
            v-show="spinShow==false" 
            v-bind:Currencyarr="Currencyarr" 
            v-bind:Subjectarr="Subjectarr"
            v-bind:Payeearr="Bookkeeping.Payeearr"
            v-bind:Payerarr="Bookkeeping.Payerarr"
            v-bind:OrderID='OrderID'
            v-bind:WaybillID='WaybillID'
            v-bind:Records.sync='Records'
            @fatherMethod="changtabledata"></Bookkeeping>
        </TabPane>
        <TabPane :label="Cashlabel" name="Cash">
          <Cash ref="Cash" v-bind:Currencyarr="Currencyarr" 
            v-bind:Subjectarr="Subjectarr"  
            v-bind:Payeearr="Cash.Payeearr"  
            v-bind:Payerarr="Cash.Payerarr"
            v-bind:OrderID='OrderID'
            v-bind:WaybillID='WaybillID'
            v-bind:Currents.sync='Currents'
            @fatherMethod="changtabledata"></Cash>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
import Cash from "./Cash";
import Bookkeeping from "./Bookkeeping";
import { currency, subject ,IncomeRecords,OutcomeRecords,IncomeParters,OutcomeParters} from "../../api/CgApi";
import { Paymentslist } from "../../api/index";
export default {
  components: {
    Cash,
    Bookkeeping
  },
  data() {
    return {
      otype:"",
      spinShow:false,
      Bookkeepinglabel: "记账（应收）",
      Cashlabel: "现金（实收）",
      getformdata: "",
      Currencyarr: [], //币种
      Subjectarr: [],//科目
      Bookkeeping:{
        Payee:"",//	收款人
        Payer:"",//付款人
        Payeearr:[],//收款人
        Payerarr:[],//付款人
      },
      Cash:{
        Payee:"",//	收款人
        Payer:"",//付款人
        Payeearr:[],//收款人
        Payerarr:[],//付款人
      },
      Payee:"",//	收款人
      Payer:"",//付款人
      OrderID:'',//订单号
      WaybillID:"",//运单ID
      Records:[],//记账
      Currents:[],//现金
      Source:"",
    };
  },
  created() {
    this.otype=this.$route.query.otype
    this.OrderID=this.$route.query.OrderID
    this.WaybillID=this.$route.query.webillID
    this.Source=this.$route.query.Source
    this.changtabledata()
  },
  mounted() {
    if (this.$route.query.type == "in") {
      this.Bookkeepinglabel = "记账（应收）";
      this.Cashlabel = "现金（实收）";
      this.getlistdata(this.$route.query.type)
      this.IncomeParters(this.$route.query.webillID)     
      // this.IncomeRecords(this.WaybillID)
      // this.Payee=this.Bookkeeping.Payeearr[0].ID

    } else {
      this.Bookkeepinglabel = "记账（应付）";
      this.Cashlabel = "现金（实付）";
      this.getlistdata(this.$route.query.type)
      this.OutcomeParters(this.$route.query.webillID)
      // this.OutcomeParters(this.WaybillID)
    }
    this.currency_subject();
    this.getsubject(this.$route.query.type);
  },
  methods: {
    changetab(name) {
      this.changtabledata()
    },
    changtabledata(){
      if(this.$route.query.type == "in"){
          this.IncomeRecords(this.WaybillID)
      }else{
        this.OutcomeParters(this.WaybillID)
      }
    },
    getdata_in_from(waybillid, type) {
      //获取数据 明细清单
      var data = {
        waybillid: waybillid,
        type: type, //收入/支出
        otype: "in" // 入库/出库
      };
      Paymentslist(data).then(res => {
        this.getformdata = res;
        this.$refs.Bookkeeping.OrderID = this.getformdata.OrderID; //订单号
        this.$refs.Bookkeeping.WaybillID = this.getformdata.WaybillID;
        this.$refs.Cash.OrderID = this.getformdata.OrderID; //订单号
        this.$refs.Cash.WaybillID = this.getformdata.WaybillID;
        if (type == "in") {
          this.$refs.Bookkeeping.Payer = this.getformdata.ClientName; //付款方
          this.$refs.Bookkeeping.Payee = this.getformdata.PayeeName; //收款方
          this.$refs.Bookkeeping.payerID = this.getformdata.ClientID; //付款方ID
          this.$refs.Bookkeeping.PayeeNameID = this.getformdata.PayeeID; //收款方ID

          this.$refs.Cash.Payer = this.getformdata.ClientName; //付款方
          this.$refs.Cash.Payee = this.getformdata.PayeeName; //收款方
          this.$refs.Cash.payerID = this.getformdata.ClientID; //付款方ID
          this.$refs.Cash.PayeeNameID = this.getformdata.PayeeID; //收款方ID
        } else {
          this.$refs.Bookkeeping.Payer = this.getformdata.ThirdName; //付款方
          this.$refs.Bookkeeping.Payee = this.getformdata.CarrierName; //收款方
          this.$refs.Bookkeeping.payerID = this.getformdata.ThirdID; //付款方ID
          this.$refs.Bookkeeping.PayeeNameID = this.getformdata.CarrierID; //收款方ID

          this.$refs.Cash.Payer = this.getformdata.ThirdName; //付款方
          this.$refs.Cash.Payee = this.getformdata.CarrierName; //收款方
          this.$refs.Cash.payerID = this.getformdata.ThirdID; //付款方ID
          this.$refs.Cash.PayeeNameID = this.getformdata.CarrierID; //收款方ID
        }
        this.spinShow=false;
      });
    },
    currency_subject() {
      //币种
      currency().then(res => {
        this.Currencyarr = res.obj;
        // this.$refs.Bookkeeping.Currencyarr = this.Currencyarr;
        // this.$refs.Cash.Currencyarr = this.Currencyarr;
      });
    },
    getsubject(val,type) {
      var conductdes=''
      if(type==30){
        conductdes='代报关'
      }else{
        conductdes='代仓储'
      }
      var data = {
        type: val,
        conduct:conductdes
      };
      console.log(data);
      subject(data).then(res => {
        this.Subjectarr = res;
        // this.$refs.Bookkeeping.Subjectarr = this.Subjectarr;
        // this.$refs.Cash.Subjectarr = this.Subjectarr;
        // console.log(this.subject)
      });
    },
    getlistdata(type){
      // console.log(this.$route.query.webillID)
      if(type=='in'){
        this.IncomeRecords(this.$route.query.webillID)
      }else{
        this.OutcomeRecords(this.$route.query.webillID)
      }
    },
    IncomeRecords(id){ //收入记录
      IncomeRecords(id).then(res=>{
        console.log(res)
        if(res.success==true){
           this.Records=res.data.Records;//记账
           this.Currents=res.data.Currents;//现金
        }
      })
    },
    OutcomeRecords(id){ //支出记录
      OutcomeRecords(id).then(res=>{
        if(res.success==true){
           this.Records=res.data.Records;//记账
           this.Currents=res.data.Currents;//现金
        } 
      })
    },

    IncomeParters(id){ //收入----收款人 付款人 
      IncomeParters(id,this.otype).then(res=>{
          this.Bookkeeping.Payeearr=res.data.Record.Payees;
          this.Bookkeeping.Payerarr=res.data.Record.Payers;
          this.Cash.Payeearr=res.data.Current.Payees;
          this.Cash.Payerarr=res.data.Current.Payers
          this.$refs.Cash.PayeeID=res.data.Current.Payees[0].ID
          this.$refs.Cash.PayerID=res.data.Current.Payers[0].ID
          this.$refs.Bookkeeping.PayeeID=res.data.Record.Payees[0].ID
          this.$refs.Bookkeeping.PayerID=res.data.Record.Payers[0].ID
      })
    },
    OutcomeParters(id){ //支出---收款人 付款人
      OutcomeParters(id,this.otype).then(res=>{
          this.Bookkeeping.Payeearr=res.data.Record.Payees;
          this.Bookkeeping.Payerarr=res.data.Record.Payers;
          this.Cash.Payeearr=res.data.Current.Payees;
          this.Cash.Payerarr=res.data.Current.Payers
          this.$refs.Cash.PayeeID=res.data.Current.Payees[0].ID
          this.$refs.Cash.PayerID=res.data.Current.Payers[0].ID
          this.$refs.Bookkeeping.PayeeID=res.data.Record.Payees[0].ID
          this.$refs.Bookkeeping.PayerID=res.data.Record.Payers[0].ID

          
          // this.Bookkeeping.Payeearr=res.data.Record.Payees;
          // this.Bookkeeping.Payerarr=res.data.Record.Payers;
          // this.Cash.Payeearr=res.data.Current.Payees;
          // this.Cash.Payerarr=res.data.Current.Payers;

          // this.$refs.Cash.PayeeID=this.Cash.Payeearr[0].ID
          // this.$refs.Cash.payerID=this.Cash.Payerarr[0].ID
          // this.$refs.Bookkeeping.PayeeID=this.Bookkeeping.Payeearr[0].ID
          // this.$refs.Bookkeeping.payerID=this.Bookkeeping.Payerarr[0].ID
      })
    }
  }
};
</script>



