<style scoped>
.inputwidth {
  width: 260px;
  padding-right: 30px;
}
.frombox {
  padding-top: 20px;
}
.iteminput {
  display: inline-block;
  padding-bottom: 10px;
}
.spanMust {
  color: red;
}
.setpadding_bottom{
  padding-bottom: 15px;
  font-size: 16px;
}
</style>
<template>
  <div class="cashbox">
   
    <div>
      <div>
        <h1 class="setpadding_bottom">费用录入</h1>
        <p class="iteminput">
          <label for>收款人：</label>
          <!-- <Input v-model="Payee" placeholder="请输入收款人" class="inputwidth" /> -->
          <Select
            v-model="PayeeID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payeearr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payeearr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
        </p>
        <p class="iteminput">
          <label for>付&nbsp;款&nbsp;&nbsp;人：</label>
          <!-- <Input v-model="Payer" placeholder="请输入付款人" class="inputwidth" /> -->
          <Select
            v-model="PayerID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payerarr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payerarr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>科&nbsp;&nbsp;&nbsp;目：</label>
          <Select
            v-model="Subject"
            filterable
            class="inputwidth"
            transfer
            @on-change="change_subject"
          >
            <Option
              v-for="(item,index) in this.Subjectarr"
              :value="item.Name"
              :key="item.index"
            >{{ item.Name }}</Option>
          </Select>
        </p>
        <p class="iteminput" v-if="Labelfee==true">
          <label for><span class="spanMust">*</span>数&nbsp;&nbsp;&nbsp;量：</label>
          <Input v-model="Labeltotal" placeholder="请输入金额" @on-blur="Labelchange(Labeltotal)"  class="inputwidth" />
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>币&nbsp;&nbsp;&nbsp;种：</label>
          <Select v-model="Currency" filterable class="inputwidth" transfer>
            <Option
              v-for="(item,index) in Currencyarr"
              :value="item.value"
              :key="item.index"
            >{{ item.name }}</Option>
          </Select>
        </p>
        <p class="iteminput">
          <label for><span class="spanMust">*</span>金&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;额：</label>
          <Input v-model="RightPrice" @on-blur="testprice(RightPrice)" placeholder="请输入金额" class="inputwidth" />
        </p>
        <Button type="primary" @click="submit_btn">提交</Button>
      </div>
      <div>
        <h1 class="setpadding_bottom">流水列表</h1>
        <Table stripe :columns="columns1" :data="Currents" max-height="380">
          <template slot-scope="{ row ,index}" slot="Indexs">
            <span>{{index+1}}</span>
          </template>
          <template slot-scope="{ row }" slot="type">
            <span>{{row.Conduct}}</span>
          </template>
          <template slot-scope="{ row }" slot="Subject">
            <span>{{row.Subject}}</span>
          </template>
          <template slot-scope="{ row }" slot="RightPrice">
            <span>{{row.Price}}</span>
          </template>
          <template slot-scope="{ row }" slot="createdata">
            <span>{{row.CreateDate}}</span>
          </template>
          <template slot-scope="{ row }" slot="user">
            <span>{{row.Creator}}</span>
          </template>
        </Table>
      </div>
    </div>
  </div>
</template>
<script>
import { currency, subject, submitfee } from "../../api/CgApi";

export default {
   props: ["Currencyarr", "Subjectarr",'Payeearr','Payerarr','OrderID','WaybillID','Currents'],
  data() {
    return {
      Labeltotal:'',
      Labelfee:false, //是否是处理标签费
      spinShow:true,
      Subject: "", //科目
      Currency: 3, //币种 默认港币
      RightPrice: "", //实收 实付金额
      disabledis: false, //根据科目判断是否可修改
      columns1: [
        {
          title: "#",
          slot: "Indexs",
          align: "center",
        },
        {
          title: "业务",
          slot: "type",
          align: "center"
        },
        {
          title: "科目",
          slot: "Subject",
          align: "center"
        },
        {
          title: "金额",
          slot: "RightPrice",
          align: "center"
        },
        {
          title: "发生时间",
          slot: "createdata",
          align: "center"
        },
        {
          title: "录入人",
          slot: "user",
          align: "center"
        }
      ],
      data1: [
        {
          name: "John Brown",
          age: 18,
          address: "New York No. 1 Lake Park",
          date: "2016-10-03"
        },
        {
          name: "Jim Green",
          age: 24,
          address: "London No. 1 Lake Park",
          date: "2016-10-01"
        },
        {
          name: "Joe Black",
          age: 30,
          address: "Sydney No. 1 Lake Park",
          date: "2016-10-02"
        },
        {
          name: "Jon Snow",
          age: 26,
          address: "Ottawa No. 2 Lake Park",
          date: "2016-10-04"
        }
      ],
      cityList: [
        {
          value: "New York",
          label: "New York"
        },
        {
          value: "London",
          label: "London"
        },
        {
          value: "Sydney",
          label: "Sydney"
        },
        {
          value: "Ottawa",
          label: "Ottawa"
        },
        {
          value: "Paris",
          label: "Paris"
        },
        {
          value: "Canberra",
          label: "Canberra"
        }
      ],
      PayeeID:"",//收款人
      PayerID:"",//付款人
    };
  },
  watch:{
      theUrl:{
        Currents(newValue, oldValue) {          
           //刷新audio的配置，不让它受上一条影响
          this.refreshAudio()
            //把新拿到的值更新到url上
          this.url = (newValue!="")? this.Commonconst.recordForwardPrefix + newValue : ''
        },
        deep: true
      }
    },
  mounted() {
      
  },
  methods: {
    testprice(value){  //验证金额
        if(value!=''){
            // var testis=/(^([-]?)[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^([-]?)(0){1}$)|(^([-]?)[0-9]\.[0-9]([0-9])?$)/; 
           var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
            var Result = testis.test(value)
            if(Result==false){
                this.RightPrice='';
                this.$Message.error('请输入正确的金额，金额保留到小数点后两位');
            }
        }
     },
     Labelchange(val){
      if(val!=''){
            // var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
            var testis=/^[0-9]*$/
            var Result = testis.test(val)
            if(Result==false){
                this.RightPrice='';
                this.$Message.error('请输入处理标签的数量');
            }else{
               this.disabledis = true;
               this.Currency = 1;
               if(val<=10){
                 this.RightPrice=100
               }else{
                 var num1=val-10
                 var number=num1*5
                 this.RightPrice=100+number
               }
               
               
            }
      }
     },
    change_subject(value) {
      if(value=='处理标签费'){
          this.Labelfee=true;
      }
      var newarr = [];
      newarr = this.Subjectarr.filter(
        item => item.Name == value && item.PayQuote.Price != null
      );
      console.log(newarr);
      var obj = newarr[0];
      if (newarr.length > 0) {
        this.disabledis = true;
        this.Currency = obj.PayQuote.Currency;
        this.RightPrice = obj.PayQuote.Price;
        // this.rightprice=obj.PayQuote.Price;   //实收实付
        // this.rightprice="";   //实收实付
        // this.leftprice=obj.PayQuote.Price;
        // this.formdata.currency=obj.PayQuote.Currency;
      } else {
        this.disabledis = false;
        this.Currency = 3;
        this.RightPrice = "";
      }
    },
    currency_subject() {
      //币种
      currency().then(res => {
        this.Currencyarr = res.obj;
      });
    },
    submit_btn() {
      if(this.Subject==""||this.Currency==""||this.RightPrice==""){
         this.$Message.error('请输入必填项');
      }else{
        if(this.Subject=='处理标签费'&&this.Labeltotal==''){
           this.$Message.error('请输入处理标签的个数');
        }else{
          var fee = {
            Currency: this.Currency, //币种
            FeeType: this.$route.query.type, //类型 in out
            Files: [], //文件
            LeftPrice: this.RightPrice, //应收 应付
            OrderID: this.OrderID, //订单id
            Payee: this.PayeeID, //收款人
            Payer: this.PayerID, //付款人
            RightPrice: this.RightPrice, //实收实付
            Subject: this.Subject, //科目
            WaybillID: this.WaybillID,
            Quantity:this.Labeltotal
          };
            submitfee(fee).then(res => {
              if (res.success == true) {
                this.$Message.success("保存成功");
                this.Subject = ""; //科目
                this.Currency = 3; //币种 ,默认是港币
                this.LeftPrice = ""; //实收 实付金额
                this.$emit('fatherMethod');
              } else {
                this.$Message.error("保存失败，请核实信息是否正确");
              }
            });
        }
        
      }
      
    }
  }
};
</script>


